body {
  margin: 0;
  padding: 0;
  font-family:  "Helvetica Neue Light", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: white;
  height: 100vh;
  margin: -40px;
}

.login {
  padding: 40px;
}

.homepage-logo{
  margin-top: 40px;
}

.overview-map {
  margin: 20px;
  height: 38vh;
}

.overview-table {
  margin: 20px;
  height: 38vh;
  }

.overview-table tr:hover{
  cursor: pointer;
  background-color: lightgrey;
}

.overview-table tr.active{
  cursor: pointer;
  background-color: lightgrey;
}

.table-container tbody {
  overflow-y: scroll;
}

.bold{
  font-weight: 700;
}

.overview-box {
  background-color: lightgrey;
  margin: 20px;
  padding-top: 12%;
  height: 12.5vh;
  border-radius: 5px;
}

.overview-number {
  font-size: 30px;
}

.overview-desc {
  font-size: 20px;
}

.overview-systemwarning {
  margin-top: 42vh;
  height: 30vh;
  font-size: 13px;
  padding: 20px;
}
.systemwarning {
  background-color: lightgrey;
  width: 100%;
  text-align: left;
  border-radius: 10px;
}

.systemwarning > .details {
  margin-top: 20px;
  overflow-y: auto;
  height: 80%;
}



.settings-sidebar{
  width: 400px;
  background-color: darkgrey;
  z-index: 5;
  right:0;
  top:0;
  height: 100vh;
  padding: 20px;
  padding-top: 60px;
  position: absolute;
  transition-duration: 0.5s;
}

.settings-sidebar.hidden {
  right:-400px;
}

.settings-sidebar small {
  position: absolute;
  bottom: 30px;
  margin-left: 30px;
}

.settings-sidebar h3{
  padding: 10px;
}

.settings-sidebar p {
  margin-top: 10px;
  padding: 10px;
}

.chart {
  margin-top: 20px;
  height: 35vh;
}

.analytics-systemwarning {
  height: 30vh;
  font-size: 13px;
  padding: 20px;
}

.analytics-info {
  margin: 20px;
  width: 105%;
}

.analytics-status{
  margin-top: 40px;
}

.node-status {
  background-color: lightgrey;
  border-radius: 5px;
  width: 100%;
  margin: 0 5px 5px 0;
  padding: 10px 3px;
  font-weight: 400;
  height: 140px;
}

.node-indicator {
  height: 30px;
  border-radius: 8px;
  width: 60%;
  margin: 15px 20%;
}

.node-indicator.low{
  background-color: green;
}

.node-indicator.moderate{
  background-color: yellow;
}

.node-indicator.high{
  background-color: orange;
}

.node-indicator.extreme{
  background-color: red;
}

.node-number {
  background-color: lightgrey;
  border-radius: 5px;
  width: 100%;
  margin: 5px 5px 5px 0;
  padding: 5px 3px;
  font-weight: 400;
  height: 215px;
}

.modeselector-toggle{
  padding-top: 5px;
}

.info-container {
  height: 50%;
  overflow-y: auto;
}

.maptable-toggle {
  height: 20px ;
  margin-top: 10px;
  margin-left: 10px;
}

.maptable-toggle span {
  padding: 0 20px ;

}
.nav {
  padding-bottom: 20px;
  padding-top: -30px;
}

.navigation {
  line-height: 95px;
  font-size: 25px;
  font: Helvetica Neue Light;
  color: grey;
}

.navigation.active{
  color: black;
}

.navigation:hover{
  color: darkgrey;
}




* {
  box-sizing: border-box;
}

.dayrange {
  padding: 0px 5px;
}

