.datetime-picker .time {
  border-top: 1px solid #efefef;
  padding: 4px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}
.datetime-picker .time .show-time {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
  font-size: 14px;
  text-align: center;
  margin: 0 0 0 10px;
}
.datetime-picker .time .show-time .text {
  line-height: 1;
  font-size: 19px;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  position: relative;
  z-index: 1;
